.box {
  width: 25px;
  height: 25px;
  outline: 1px solid rgb(113, 145, 172);
  display: inline-block;
  background-color: rgb(211, 211, 212);
  cursor: pointer;
  -webkit-touch-callout: none;
  -ms-user-select: none;
  -webkit-user-select: none;
          user-select: none;
}

.finish {
  background-image: url(/static/media/bullseye-solid.8a77098a.svg);
  background-repeat: no-repeat;
  text-align: center;
  cursor: -webkit-grab;
  cursor: grab;
}

.start {
  background-image: url(/static/media/play-solid.3c7578b0.svg);
  background-repeat: no-repeat;
  text-align: center;
  cursor: -webkit-grab;
  cursor: grab;
}

.wall {
  background-color: black;
}

.visited {
  background-color: #2a4e9c;
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
}

@keyframes visitedAnimation {
  0% {
    background-color: rgb(95, 202, 188);
    border-radius: 150%;
  }
  50% {
    background-color: rgb(67, 67, 167);
    border-radius: 50%;
  }
  100% {
    background-color: #1e3669;
    border-radius: 0%;
  }
}

.path {
  background-color: rgb(99, 241, 80);
}

@media screen and (max-width: 1255px) {
  .box {
    width: 2%;
  }
}

.header {
  height: 80px;
  background-color: #3a3f4b;
  font-size: 18px;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.header-container {
  display: inline-block;
}

.btn {
  font-size: 18px;
  padding: 11px;
  background-color: white;
  display: inline-block;
  border-radius: 5px;
  margin: 15px;
}

button:hover {
  background-color: rgb(176, 197, 218);
  transition: 0.2s ease-out;
}

button:active {
  color: black;
}

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #4f5a72;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.grid {
  display: block;
  width: 1250px;
  margin-top: 3rem;
  margin-bottom: 3rem;
  background-color: rgb(211, 211, 212);
}

